
'use client';

import React, { useState, useEffect, Suspense } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star, Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePageBackground';
import { useRouter } from 'next/navigation';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import type { ProfileData } from '@/types';
import type { User } from 'firebase/auth';

declare global {
  interface Window {
    Razorpay: any;
  }
}

const CustomRazorpaySubscription = ({ planId, user, profile }: { planId: string; user: User; profile: ProfileData; }) => {
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();
  const router = useRouter();

  const loadRazorpayScript = (): Promise<boolean> => {
    return new Promise((resolve) => {
      if (window.Razorpay) {
        resolve(true);
        return;
      }

      const script = document.createElement('script');
      script.src = 'https://checkout.razorpay.com/v1/checkout.js';
      script.async = true;
      
      script.onload = () => resolve(true);
      script.onerror = () => resolve(false);
      
      document.body.appendChild(script);
    });
  };

  const handleSubscription = async () => {
    setIsLoading(true);
    
    try {
      console.log('Starting subscription process...');
      
      const requestData = {
        planId,
        firebase_uid: user.uid,
        customerEmail: profile.email,
        customerName: `${profile.firstName || ''} ${profile.surname || ''}`.trim() || profile.email,
        customerContact: profile.mobileNo || undefined,
      };
      
      console.log('Request data:', requestData);

      const response = await fetch('/api/create-subscription', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestData),
      });

      console.log('API Response status:', response.status);
      
      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        const textResponse = await response.text();
        console.error('Non-JSON response received:', textResponse);
        throw new Error('Server returned invalid response format. Check server logs.');
      }

      const responseData = await response.json();
      console.log('API Response data:', responseData);

      if (!response.ok) {
        throw new Error(responseData.error || `HTTP error! status: ${response.status}`);
      }

      const { subscriptionId } = responseData;
      
      if (!subscriptionId) {
        throw new Error('No subscription ID received from server');
      }

      const scriptLoaded = await loadRazorpayScript();
      if (!scriptLoaded) {
        throw new Error('Failed to load Razorpay checkout script');
      }

      const options = {
        subscription_id: subscriptionId,
        name: 'Abacus Pro',
        description: 'Monthly Subscription to Abacus Pro',
        handler: function (response: any) {
          console.log('Payment successful:', response);
          setIsLoading(false);
          router.push('/payment-success?razorpay_payment_id=' + response.razorpay_payment_id + '&razorpay_subscription_id=' + response.razorpay_subscription_id);
        },
        modal: {
          ondismiss: function() {
            console.log('Payment modal dismissed');
            setIsLoading(false);
          }
        },
        prefill: {
          name: `${profile.firstName || ''} ${profile.surname || ''}`.trim() || profile.email,
          email: profile.email,
          contact: profile.mobileNo || '',
        },
        notes: {
          firebase_uid: user.uid,
          customer_email: profile.email,
        },
        theme: {
          color: '#F43F5E' 
        }
      };

      console.log('Opening Razorpay checkout with options:', options);
      const rzp = new window.Razorpay(options);
      rzp.open();

    } catch (error: any) {
      console.error('Subscription error:', error);
      setIsLoading(false);
      
      toast({
        title: "Subscription Error",
        description: error.message || "Something went wrong. Please try again.",
        variant: "destructive",
      });
    }
  };

  return (
    <Button 
      onClick={handleSubscription}
      disabled={isLoading}
      className="w-full"
      size="lg"
    >
      {isLoading ? (
        <>
          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          Processing...
        </>
      ) : (
        'Subscribe Now'
      )}
    </Button>
  );
};


